import React from 'react'
import { ThreeDots } from  'react-loader-spinner'
import './loader.css'
function Loader() {
  return (
    <div id='loader' >
        <ThreeDots 
height="80" 
width="80" 
radius="9"
color="rgb(0, 157, 107)" 
ariaLabel="three-dots-loading"
wrapperStyle={{}}
wrapperClassName=""
visible={true}
 />
    </div>
  )
}

export default Loader;