import React from 'react'
import { useEffect ,useState } from 'react'
import profit from './profit.png'
import lose from './loose.png'
import { Link } from 'react-router-dom'
import "./table.css"
const Coinpage = () => {
    const[page,setpage]=useState(10);


    useEffect(() => {
      
    
    crypto()  
        
      
    }, [page])

    

    
const[api,setapi]=useState([])    

    const crypto=async()=>{
const res = await fetch(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=inr&order=market_cap_desc&per_page=${page}&x_cg_demo_api_key=CG-8MoKWLA7v67FsK5EoQBv5kx5`)
const data= await res.json()   
setapi(data)

}


const loadmore=()=>{
    setpage(page+10)
}
// const price = price_change_percentage_24h
// console.log(api[9].symbol);
    return (
     
     <>
    <div id='full' className='container pb-5 mt-5  ' >
   <table width={'100%'}  >
        
        {/* https://static.vecteezy.com/system/resources/thumbnails/012/177/434/small/candlestick-graph-icon-png.png */}
    
        {

    api.map((cry=>
<tr id='datatable' className='  ' >

<td  className='text-light pb-1 text-start pt-5 fs-1 ' >
<img className=' m-4 ' src={cry.image} height="40px" width={"40px"} alt="" />
<span className='fw-lighter' >{cry.name}</span>

<span id='symbol' className='fs-1 ms-3 ' >({cry.symbol})</span>
</td>

<td className='text-light fs-3 fw-bold pt-5 text-center' >
₹{cry.current_price}
</td>
<td  style={{color:cry.price_change_percentage_24h>0?"green":"red"}}  className='text-center fs-4 pt-5' >
{cry.price_change_percentage_24h}%
</td>

{/* <td className='text-light pt-5 ' >
    {cry.market_cap_change_24h}
</td> */}
<td className='pt-5 justify-content-end ' >
    <img height={"40px"} width={"120px"} src={cry.price_change_percentage_24h>0?profit:lose} alt="" />
</td>

<td className='pt-5' >
<Link style={{textDecoration:'none'}} to={`/card/${cry&&cry.id}`} >

    <button className='btn btn-success  ' >
MORE..
    </button>
</Link>

</td>



</tr>
 
    ))
}

</table>  

<div className="text-end me-5  mt-5 ">
    <button id='loadmore' className='px-4 py-1 fs-4'  onClick={loadmore} >
        load more...
    </button>
</div>

</div>


</>
  )
}

export default Coinpage
