import React from 'react'
import { useEffect,useState } from 'react';
import { useParams } from 'react-router-dom';
import Chart from './Chart';
import './chart.css'
import axios from 'axios'
const Coininfo = () => {
    const[imagess,setimage]=useState([])
    const[description,setdescription]=useState([])
    const[coin,setcoin]=useState([])
    const[price,setprice]=useState([])
    const[cap,setmarketcap]=useState([])
    const[per,setper]=useState([])

    const {id}=useParams()
    useEffect(()=>{
        const coindata=async()=>{


            try{
                const {data}=await axios.get(`https://api.coingecko.com/api/v3/coins/${id}`)
                setimage(data.image);
                setdescription(data.description.en)
                setcoin(data)
                setprice(data.market_data.current_price)
                setmarketcap(data.market_data.market_cap)
                setper(data.market_data)


            }
            catch(error){
                console.log(error);
            }
             }
             coindata()

    },[id])
    // console.log(coin.market_data.price_change_percentage_24h);

// console.log(coin.symbol);
// console.log(coin.public_interest_score);

// console.log(price);
    return (<>
        
        
        <div className='pt-4' id='mainchart' >
            
    

<div className="row  pt-5">

<div id='chartfirst' className="col-md-3 text-light  mt-5 text-center ">
<img height={"130px"} width={"130px"}  src={ imagess.large } alt="" />
{/* <h1 className='text-uppercase' >{coin.symbol}</h1> */}
<p id='description' >
    {description}.
</p>
<h2 className='text-start ms-3 fw-bold ' > RANK: <span id='value' className='fs-3' > {coin.market_cap_rank}</span> </h2>
<h2 className='text-start ms-3 my-4 fw-bold text-uppercase ' >  price: <span id='value' className='fs-3  ' >{price.inr}₹</span> </h2>
<h2    className='text-start ms-3 fw-bold text-uppercase ' >  growth: <span id='value' style={{color:per.price_change_percentage_24h>0?"green":"red"}} className='fs-3' >   {per.price_change_percentage_24h}%</span> </h2>

{/* <h2 className='text-start ms-3' > RANK: {coin.market_cap_rank} </h2> */}

</div>

<div className="col-md-9">
<div id="chartsk">

<Chart/>
</div>

</div>

    </div>
    </div>

        
        
        
        
        
    <div>

    </div>
    </>
  )
}

export default Coininfo;
