import React from 'react';
import "./chart.css";
import { useEffect,useState } from 'react'
import { useParams } from 'react-router-dom'
import axios from 'axios';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
  } from 'chart.js';
  import { Line } from 'react-chartjs-2';
  
  
  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
  );
const Chart = () => {
  const{id}=useParams()
  const [days, setDays]=useState(1)
  useEffect(()=>{
    chartdata()
    coindata()
    // coindata()
  },[id,days])
  const[datas,setdata]=useState([''])
  const[per,setper]=useState([''])



  
  const coindata=async()=>{


    try{
        const {data}=await axios.get(`https://api.coingecko.com/api/v3/coins/${id}`)
        setper(data.market_data)
    
    }catch(error){
            console.log(error);
        }}
const chartdata=async()=>{
    const res = await fetch(`https://api.coingecko.com/api/v3/coins/${id}/market_chart?vs_currency=inr&days=${days}`)
const data= await  res.json();
setdata(data.prices)
// console.log(data);
}

console.log(per.price_change_percentage_24h);
const myData = {
    labels: datas.map((value)=>{
     const date = new Date(value[0])
     const time = 
     date.getHours()> 12 
     ? `${date.getHours() -12} : ${date.getMinutes()} PM` 
     : `${date.getHours()} : ${date.getMinutes()} AM`;
      return days===1 ? time : date.toLocaleDateString() 
    }), 
    datasets:[
        {
            label: ` Price in Past ${days} days in ₹`,
            data: datas.map((value)=>value[1]),
            borderColor:per.price_change_percentage_24h>0?'green':'red',
            borderWidth: '2' 
        }
    ]
    
  }

 
// const img = coin.image.large;

    return (
<>
<div className="mainchart">
<div className="fullchart">
<Line  id='line' data={myData} options={{
            elements:{
                point:{
                    radius:1,
                }
            }
        }}  />
        </div>
        <div className="row justify-content-start ms-5 mt-1 ">
    <button id='days' onClick={()=>{setDays(1)}}  className='btn active text-light fw-bold  col-3 m-1 ' >24h</button>
    <button id='days' onClick={()=>{setDays(30)}}  className='btn  text-light fw-bold  col-3 m-1 ' >30 days</button>
    {/* <button onClick={()=>{setDays(60)}}  className='btn text-light fw-bold  col-2 m-1' >1 year</button> */}
    <button id='days' onClick={()=>{setDays(365)}}  className='btn text-light fw-bold  col-3 m-1' >1 year</button>


</div>
</div>
</>
  )
}
export default Chart;